import React, { useState, useEffect } from 'react';
import { LineGraph, OptimalAgeGraph, DelayedOptimalAgeGraph } from './Components/BenefitsGraphs';
import * as Styled from './App.styled';
import './App.css';
import { Dropdown } from './Components/Dropdown';

const birthYears = [
  "1943 to 1954",
  "1955",
  "1956",
  "1957",
  "1958",
  "1959",
  "1960 or later"
]

const App = () => {
  const [birthYear, setBirthYear] = useState("1943")
  const [monthlyBenefit, setMonthlyBenefit] = useState(1000)
  
  const handleBirthYearChange = birthYear =>
    setBirthYear(birthYear.slice(0, 4))
  
  const handleInput = input => 
    parseInt(input) === NaN ? null : setMonthlyBenefit(parseInt(input))
  
  return (
    <Styled.DocumentContainer>
      <h4>What the heck am I looking at?</h4>
        These are a series of graphs meant to make the process of estimating SSI benefits 
        a little less painful. There are three graphs; the first shows an expected monthly amount based on the age in which you decide to begin withdrawing your SSI benefits. The second graph is an estimation of at least how much you could expect to make, <i>in total</i>, from the time you begin withdrawing up to some number of years into the future. Lastly, the third is exactly the same as the second, except that it takes into account <i>delayed</i> retirement benefits, which begin at the full retirement age of 67 and continue until age 70, at which point they no longer increase.
        <br />
      <h4>Why?</h4>
        While all of the needed information is clearly laid out on the official SSA website, it's not terribly straightforward as to what one could reasonably expect only by looking through ages and percentages. By presenting the information in a series of graphs, it becomes <i>much</i> easier to gauge how <i>reduced</i> and <i>delayed</i> benefits actually impact one's future quality of life.
        <br /><br />
      <h4>Very important disclaimer:</h4>
        These amounts are based on a standard figure of <b>$1,000 per month</b>. The SSA does a few extra calculations depending on how much you've made over the past 35 years, including earned and unearned income, whether or not you're a government worker, and so on. Additionally, a <i>Cost-of-Living Adjustment</i> (COLA) is applied to your payments, which is meant to compensate for inflation. <i>There are no such calculations or adjuments here.</i> This is only meant to give an overview of how SSI benefits vary with respect to one's retirement age.
      <br />
        <br />
        {/* To get started, just select your year of birth:
      <Dropdown options={birthYears} setParentValue={handleBirthYearChange} />
        <br />
        If you've calculated your own monthly benefit amount, please feel free to enter it below, and the numbers will change accordingly:
        <br />
      <input
        defaultValue={1000}
        onChange={e => handleInput(e.target.value)}
        style={{
          width: "70px",
          textAlign: "center"
        }}
      /> */}
      <Styled.GraphContainer>
        <LineGraph
          monthlyBenefit={monthlyBenefit}
        />
      </Styled.GraphContainer>
        This is a graph displaying the total amount one would receive up to a certain age. 
      <Styled.GraphContainer>
        <OptimalAgeGraph />
      </Styled.GraphContainer>
        This is a similar graph for post-full retirement age.
      <Styled.GraphContainer>
        <DelayedOptimalAgeGraph />
      </Styled.GraphContainer>
    </Styled.DocumentContainer>
  );
}

export default App;